import {FaTimes} from 'react-icons/fa'
const Task = ({t,onDelete}) => {
    function del(id){
        document.getElementById('task').innerHTML += ""
    }
  return (
    <div id="task"style={{color:'red',background:'black'}} >
      
      <p>{t.text}<FaTimes  onClick={()=>onDelete(t.id)}/></p>
      <p>{t.id}</p>
    </div>
  )
}

export default Task
